import React from 'react';
import classes from './Posts.module.css';
import ava from '../../assets/avaImg/mal2.jpg';

const Posts = (props) => {

    if (!props.profile) {
        return null
    }

    return (
        <div className={classes.Posts}>
            <div>
                <div className={classes.PostImg}>
                    <img src={props.profile.photos.large ? props.profile.photos.large : ava} />
                </div>
                <div className={classes.likesZone}>
                    <div className={classes.likesWord}>
                        нравится:
                    </div>
                    <div className={classes.likes}>
                        {props.likes}
                    </div>
                </div>
                {props.isOuner ? <button onClick={() => props.deletePostAC(props.id)}>удалить пост</button> :
                    <button>лайк</button>}
            </div>
            <div className={classes.postMessage}>
                {props.message}
            </div>
        </div>

    )
};

export default Posts;
